import request from "@/utils/request";
import { mapTrackPlayableStatus } from "@/utils/common";

/**
 * 
 *  :  ,  /  /  /  /  ,  ,  ,
 *  "   "(  ),  mp3url  ,  /song/url  id 
 * - keywords : 
 * - limit :  ,  30
 * - offset :  ,  :  :(  -1)*30,  30  limit  ,  0
 * - type:  1  ,  : 1: , 10: , 100: , 1000: , 1002: , 1004: MV, 1006: , 1009: , 1014: , 1018:
 * -  : /search?keywords= /cloudsearch?keywords=()
 * @param {Object} params
 * @param {string} params.keywords
 * @param {number=} params.limit
 * @param {number=} params.offset
 * @param {number=} params.type
 */
export function search(params) {
  return request({
    url: "/search",
    method: "get",
    params,
  }).then((data) => {
    if (data.result?.song !== undefined)
      data.result.song.songs = mapTrackPlayableStatus(data.result.song.songs);
    return data;
  });
}

export function personalFM() {
  return request({
    url: "/personal_fm",
    method: "get",
    params: {
      timestamp: new Date().getTime(),
    },
  });
}

export function fmTrash(id) {
  return request({
    url: "/fm_trash",
    method: "post",
    params: {
      timestamp: new Date().getTime(),
      id,
    },
  });
}
